.visitor {
  z-index: 2;
  position: relative;
  width: 54%;
  display: flex;
  float: left;
  text-align: center;
  line-height: 68px;
  font-size: 16px;
  justify-content: space-around;
  background-color: rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 10px 10px rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  color: #ffffff;
}
.visitor > div {
  flex: 1;
}
.visitor > div .number {
  color: #FCC83B;
  font-size: 20px;
}
