.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper,
.container .rightWrapper {
  background-image: url("~img/leftBg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
  height: 99%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  background-image: url("~img/rightBg.png");
  float: right;
}
.container .item,
.container .itemRight {
  padding: 0 10px 5px;
  height: calc(100% / 3);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.icon {
  background-image: url("~img/Group.png");
  background-repeat: no-repeat;
  width: 23px;
  height: 10px;
  margin-right: 6px;
  margin-left: 6px;
}
.headerWrapper {
  display: flex;
  line-height: 48px;
}
.headerWrapper .titleWrapper {
  flex: 1 0 200px;
  border-bottom: 1px solid;
  margin-right: 10px;
  border-image: linear-gradient(to left, #6cf5b0, #6dfafe, #2ba6ff) 1;
}
.headerWrapper .titleWrapper > div {
  display: inline-block;
}
.headerWrapper .titleWrapper .title {
  color: #fff;
}
.headerWrapper .rightIconWrapper {
  border-bottom: 1px solid #CACCD2;
  text-align: center;
  width: 25px;
}
.headerWrapper .rightIconWrapper .rightIcon {
  display: inline-block;
  background-image: url("~img/Shape.png");
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
}
