.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper,
.container .rightWrapper {
  background-image: url("~img/leftBg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
  height: 99%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  background-image: url("~img/rightBg.png");
  float: right;
}
.container .item,
.container .itemRight {
  padding: 0 10px 5px;
  height: calc(100% / 3);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.eight {
  height: 100%;
}
.eight .eightContainer {
  display: flex;
  flex-direction: column;
  height: calc(100% - 50px);
  padding: 0 20px;
}
.eight .eightContainer > div {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50%;
  color: #ffffff;
}
.eight .eightContainer > div > div {
  padding-left: 15px;
}
.eight .eightContainer > div > div .title {
  text-align: center;
}
.six {
  height: 100%;
}
.six .content {
  display: flex;
  align-items: center;
  align-content: center;
  margin-top: 5px;
}
