.TrafficAccidentPage {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.TrafficAccidentPage .banner {
  flex: 1;
  display: flex;
  align-items: center;
  margin-top: 3%;
}
.TrafficAccidentPage .container {
  margin: 15px 50px;
  height: 100%;
  background: #041222;
  padding: 10px;
  position: relative;
}
.TrafficAccidentPage .imageborder {
  position: absolute;
  width: 15px;
  height: 15px;
}
.TrafficAccidentPage .imageborder1 {
  top: 0;
  left: 0;
  border-left: 2px solid #008ffc;
  border-top: 2px solid #008ffc;
}
.TrafficAccidentPage .imageborder2 {
  top: 0;
  right: -1px;
  border-right: 2px solid #008ffc;
  border-top: 2px solid #008ffc;
}
.TrafficAccidentPage .imageborder3 {
  bottom: 0;
  left: 0;
  border-bottom: 2px solid #008ffc;
  border-left: 2px solid #008ffc;
}
.TrafficAccidentPage .imageborder4 {
  bottom: 0;
  right: -1px;
  border-right: 2px solid #008ffc;
  border-bottom: 2px solid #008ffc;
}
.TrafficAccidentPage .headerBox {
  overflow: hidden;
}
.TrafficAccidentPage .headerBox .headerLeft {
  color: red;
  float: left;
  font-size: 10px;
  overflow: hidden;
}
.TrafficAccidentPage .headerBox .headerLeft .iconImg {
  display: inline-block;
  width: 20px;
  height: 15px;
  float: left;
}
.TrafficAccidentPage .headerBox .headerLeft .iconImg img {
  width: 100%;
  height: 100%;
}
.TrafficAccidentPage .headerBox .headerLeft span:nth-child(2) {
  float: left;
  height: 20px;
  line-height: 20px;
  margin-left: 10px;
}
.TrafficAccidentPage .headerBox .headerRight {
  color: red;
  float: right;
  font-size: 10px;
  height: 20px;
  line-height: 20px;
}
.TrafficAccidentPage .content {
  overflow: hidden;
  display: flex;
}
.TrafficAccidentPage .content .imgBox {
  width: 60%;
  height: 140px;
  float: left;
  padding: 10px 0px;
  box-sizing: border-box;
}
.TrafficAccidentPage .content .imgBox img {
  width: 100%;
  height: 100%;
}
.TrafficAccidentPage .content .traficDes {
  padding: 15px;
  box-sizing: border-box;
  flex: 1;
  color: #eff7fd;
  font-size: 10px;
  line-height: 20px;
}
.TrafficAccidentPage .content .traficDes p {
  margin: 0px;
  padding: 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.TrafficAccidentPage .swiperBtn {
  width: 15px;
  height: 15px;
  top: 60%;
  outline: none;
}
.TrafficAccidentPage :global(.swiper-button-prev) {
  background-size: 15px 15px;
  left: 5%;
}
.TrafficAccidentPage :global(.swiper-button-next) {
  background-size: 15px 15px;
  right: 5%;
}
