.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper,
.container .rightWrapper {
  background-image: url("~img/leftBg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
  height: 99%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  background-image: url("~img/rightBg.png");
  float: right;
}
.container .item,
.container .itemRight {
  padding: 0 10px 5px;
  height: calc(100% / 3);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.radioButton {
  background: #65B3FA !important;
  border-radius: 4px;
  outline: none;
  color: #fff;
  border-color: #65B3FA;
  border: 0;
}
.radioButton:hover {
  background-color: #fff !important;
  color: #313131;
  border-color: #313131;
  border: 0;
}
.active {
  background-color: #fff !important;
  color: #313131;
  border-color: #313131;
  border: 0;
  display: inline-block;
}
.three {
  height: calc(100% - 40px);
}
.three .threeWrapper {
  display: flex;
  justify-content: space-around;
  height: 100%;
  align-items: center;
}
.three .threeWrapper .infoItem {
  width: 48%;
}
.three .threeWrapper .infoItem .header {
  padding: 5px;
  height: 18%;
  background: #113661;
  border: 1px solid #14eeff;
  opacity: 0.5;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.three .threeWrapper .infoItem .header .camera {
  width: 13px;
  height: 13px;
}
.three .threeWrapper .infoItem .header .name {
  font-size: 14px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  color: #ededed;
}
.three .threeWrapper .infoItem .pic > video {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
