.Three {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.Three .content {
  height: 80%;
  width: 100%;
  position: relative;
}
.Three .content .imgbox {
  position: absolute;
  top: 5px;
  right: 15px;
  width: 60px;
  height: 60px;
  z-index: 2;
}
.Three .content .imgbox div {
  width: 100%;
  height: 100%;
  position: relative;
}
.Three .content .imgbox div img {
  position: absolute;
  left: 0;
  top: -10px;
  width: 60px;
  height: auto;
  opacity: 0;
}
.Three .content .videoBox {
  position: relative;
  top: 0;
  left: 0;
  z-index: 1;
  padding: 50px 20px 0 20px;
}
.Three .content .videoBox video {
  width: 100%;
  border: none;
}
