.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper,
.container .rightWrapper {
  background-image: url("~img/leftBg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
  height: 99%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  background-image: url("~img/rightBg.png");
  float: right;
}
.container .item,
.container .itemRight {
  padding: 0 10px 5px;
  height: calc(100% / 3);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.five {
  text-align: center;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
.five .infoItem {
  margin: 0px 40px;
}
.five .infoItem > div {
  margin-top: 10px;
}
.five .infoItem .name,
.five .infoItem .unit {
  font-size: 15px;
  font-family: Source Han Sans SC;
  font-weight: 400;
  color: white;
  opacity: 0.7;
}
.five .infoItem .unit {
  font-size: 13px;
  font-weight: 300;
  opacity: 0.5;
}
.five .infoItem .num,
.five .infoItem .num .smallNum,
.five .infoItem .num2 {
  font-size: 26px;
  font-family: Source Han Sans SC;
  font-weight: 400;
  color: #2ce6fc;
  display: flex;
  align-items: center;
  justify-content: center;
}
.five .infoItem .num .smallNum,
.five .infoItem .num2 .smallNum {
  padding-left: 5px;
  font-size: 15px;
  color: #fce14c;
}
.five .infoItem .num2 {
  color: #f52cfc;
}
