@charset "UTF-8";
.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper,
.container .rightWrapper {
  background-image: url("~img/leftBg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
  height: 99%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  background-image: url("~img/rightBg.png");
  float: right;
}
.container .item,
.container .itemRight {
  padding: 0 10px 5px;
  height: calc(100% / 3);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
/*当日疫情*/
.oneContainer {
  width: 100%;
  height: 100%;
}
.oneContainer .infoItemBox {
  width: calc(100% - 25px);
  height: calc(100% - 49px);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.oneContainer .infoItemBox .infoItem {
  width: 40%;
  text-align: center;
}
.oneContainer .infoItemBox .infoItem .itemNumber {
  line-height: 50px;
  font-size: 32px;
}
.oneContainer .infoItemBox .infoItem .itemName {
  font-size: 20px;
  line-height: 23px;
  color: white;
}
.iconWrapper {
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-around;
  height: 100%;
}
.iconWrapper .itemImg {
  width: 33%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.iconWrapper .itemImg .iconText,
.iconWrapper .itemImg .iconNum {
  color: #fff;
  margin-top: 3px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.iconWrapper .itemImg .iconNum {
  font-size: 30px;
  font-weight: bold;
  font-family: fantasy;
  color: #05DEFF;
}
.iconWrapper .itemImg .iconNum:hover {
  color: #FC2709;
  cursor: pointer;
}
