.serviceContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: calc(100% - 50px);
}
.serviceContainer .serviceWrapper {
  display: flex;
  font-size: 12px;
}
.serviceContainer .serviceWrapper .before {
  flex: 1 0 120px;
  color: #ffffff;
  margin-right: 2px;
}
.serviceContainer .serviceWrapper .before .number {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  background-color: #05D9F9;
  color: #000000;
  font-weight: 700;
  margin-right: 10px;
}
.serviceContainer .serviceWrapper .progress {
  margin-top: -4px;
  margin-right: 5px;
}
.serviceContainer .serviceWrapper .progress .ant-progress-inner {
  background-color: rgba(0, 0, 0, 0);
}
.serviceContainer .serviceWrapper .after {
  flex: 1 0 50px;
  color: #05DEFF;
}
.serviceContainer .serviceWrapper .after .img {
  width: 12px;
  margin-left: 5px;
}
