.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper,
.container .rightWrapper {
  background-image: url("~img/leftBg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
  height: 99%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  background-image: url("~img/rightBg.png");
  float: right;
}
.container .item,
.container .itemRight {
  padding: 0 10px 5px;
  height: calc(100% / 3);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.six {
  height: 100%;
}
.six .marquee {
  text-align: center;
  margin-top: 10px;
  color: #ffffff;
  font-size: 12px;
  line-height: 20px;
  opacity: 0.6;
  padding: 10px 20px;
  height: calc(100% - 68px);
}
.six .marquee marquee {
  text-align: center;
}
.six .marquee p {
  padding: 0 6%;
}
