.trafficAccident .mapboxgl-popup-content {
  width: 215px;
  height: 56px;
  background: url("~img/traffic/accidentPopup.png") no-repeat;
  padding: 8px 10px 15px;
}
.trafficAccident {
  height: 14px;
  font-size: 14px;
  font-family: Source Han Sans SC;
  font-weight: 500;
  color: #ffffff;
  position: relative;
  left: 45px;
}
.accidentTime {
  width: 73px;
  height: 14px;
  font-size: 13px;
  font-family: Source Han Sans SC;
  font-weight: 300;
  color: #ffffff;
  margin-left: 5px;
}
.accidentRoad {
  width: 74px;
  height: 13px;
  font-size: 13px;
  font-family: Source Han Sans SC;
  font-weight: 300;
  color: #ffffff;
  position: relative;
  left: 45px;
  top: 5px;
}
.trafficIcon .mapboxgl-popup-content {
  width: 29px;
  height: 37px;
  background: url("~img/traffic/congestionPopup.png") no-repeat;
  margin: 8px 10px 10px 4px;
}
.trafficCongestion .mapboxgl-popup-content {
  width: 215px;
  height: 56px;
  background: url("~img/traffic/congestionPopup.png") no-repeat;
  padding: 8px 10px 15px;
}
