.fourContainer {
  width: 100%;
  height: 100%;
}
.fourContainer .infoItemBox {
  width: 100%;
  height: 90%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.fourContainer .infoItemBox .infoItem {
  width: 45%;
  text-align: left;
}
.fourContainer .infoItemBox .infoItem .itemPic {
  width: 56px;
  height: 56px;
  float: left;
}
.fourContainer .infoItemBox .infoItem .itemName {
  text-indent: 16px;
  font-size: 14px;
  font-family: "Adobe Heiti Std";
  font-weight: normal;
  color: white;
}
.fourContainer .infoItemBox .infoItem .itemNumber {
  text-indent: 16px;
  font-size: 30px;
  font-family: Arial;
  font-weight: bold;
  color: #05deff;
  margin-top: 14px;
}
.fourContainer .infoItemBox .infoItem .itemUnit {
  display: inline;
  font-size: 14px;
  font-family: Arial;
  font-weight: bold;
  color: #05deff;
}
