.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper,
.container .rightWrapper {
  background-image: url("~img/leftBg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
  height: 99%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  background-image: url("~img/rightBg.png");
  float: right;
}
.container .item,
.container .itemRight {
  padding: 0 10px 5px;
  height: calc(100% / 3);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.manager .mapboxgl-popup-content {
  padding: 10px 0px 15px !important;
  opacity: 1;
  border-radius: 4px;
}
.managerPopup1 .mapboxgl-popup-content {
  width: 339px;
  height: 250px;
  background: url("~img/manager/statistics.png") no-repeat;
}
.managerPopup2 .mapboxgl-popup-content {
  width: 313px;
  height: 218px;
  background: url("~img/manager/incidents.png") no-repeat;
}
.managerPopup1 .font {
  width: 47px;
  height: 1px;
  font-size: 30px;
  font-family: PingFang SC;
  font-weight: 400;
  color: white;
  line-height: 28px;
  text-align: center;
}
.managerPopup1 .title {
  width: auto;
  height: 20px;
  font-size: 20px;
  font-family: PingFang SC;
  font-weight: 500;
  color: #00eeff;
  padding: 3px  0px 30px 53px;
}
.managerPopup1 .static {
  float: left;
  width: 2.5%;
  height: 12px;
  font-size: 20px;
  font-family: PingFang SC;
  font-weight: 400;
  color: white;
  margin-left: 50px;
  margin-top: 75px;
}
.managerPopup2 .title {
  width: auto;
  height: 20px;
  font-size: 20px;
  font-family: PingFang SC;
  font-weight: 500;
  color: white;
  text-align: center;
}
.managerPopup2 .body {
  padding-top: 7%;
}
.managerPopup2 .content {
  text-align: center;
  height: 26px;
  font-size: 15px;
  font-family: PingFang SC;
  font-weight: 400;
  color: white;
}
